import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Lower } from './lower.model';
import { Subject } from "rxjs";
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class LowerserviceService {

  private prods: Lower[] = [];
  private postsUpdated = new Subject<Lower[]>();
    url = 'http://localhost:4000/MRlogin/products'
    constructor(private http: HttpClient) { }

    Data: any;
        getPosts() {
          this.http
            .get<{ message: string; prods: any }>(
              "http://localhost:3000/api/lowers"
            )
                 .subscribe(responseData => {

                  console.log(responseData.prods);
                  this.Data = responseData.prods;
                      });
          }


        getPostUpdateListener() {
          return this.postsUpdated.asObservable();
        }



}
