
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Subject } from 'rxjs';
import { map } from 'rxjs/operators';

import { Post } from './register.model';





@Injectable({ providedIn: "root" })
export class MRRegisterService {
   // http: HttpClient
    //url = 'http://localhost:4000/login/dashboard/user'   //express port 4000
    private posts: Post[] = [];
    private postsUpdated = new Subject<Post[]>();
     constructor(private http: HttpClient) {
       // this.http = httpClient
     }

     getPosts() {
    /// three ... are used to return value // otherwise it will return reference of array
    this.http.get<{message: string, posts: any}>('http://localhost:3000/api/posts')  // hence node is accessed from angular
    // message: string, posts: post[] this tells how the format of data should be printed in the api
    .pipe(map((postData) => {
      return postData.posts.map(post => {
        return {
          username: post.username,
          firstname: post.firstname,
          lastname: post.lastname,
          joindate: post.joindate,

         phoneno: post.phoneno,
         password: post.password,
         email: post.email,
         id: post._id
        };
      });
    }))
    .subscribe(transformedPosts => {
      this.posts = transformedPosts;
      this.postsUpdated.next([...this.posts]);
    });
    // return [...this.posts];
  }


  getPostUpdateListener() {
    // this returns postsUpdated as Observable()
    return this.postsUpdated.asObservable();

  }

     addUsers( username: string, firstname: string, lastname: string, joindate: string,
        phoneno: string,
        password: string,
        email: string ,
        ){


            const post: Post = {
              id: null,
              username: username,
              firstname: firstname,
              lastname: lastname,
              joindate: joindate,
              phoneno: phoneno,
              password: password,
              email: email ,
               };
            this.http
              .post<{ message: string }>("http://localhost:3000/api/new-posts", post)
              .subscribe(responseData => {
                console.log(responseData.message);
                this.posts.push(post);
                this.postsUpdated.next([...this.posts]);

              });
        /*const body = {
            username: username,
            firstname: firstname,
            lastname:  lastname,
            joindate: joindate,
            phoneno:   phoneno,
            email:    email,
            password: password
        }*/

        //nreturn this.http.post(this.url, body)
    }








































}
