import { Component} from '@angular/core';
import { Router } from '@angular/router';
import { MRloginService } from './MRlogin.service';
import {HttpClient} from '@angular/common/http';


@Component({
    selector: 'MR-login',
    templateUrl: './MRlogin.component.html',
    styleUrls: ['./MRlogin.component.css']
})

export class MRloginComponent {
    email = ''
    password = ''
    rememberme = false
   // isLoggedIn = false



    constructor(private router: Router,public service: MRloginService,private http: HttpClient ) { }

    onlogin()//email: string, password: string )
    {

        if(this.email.length == 0)
        {
         alert('email field can not be empty')
        }
        else if(this.password.length == 0)
        {
            alert('password can not be empty')
        }
        else
        {

            const lg = {email:this.email, password:this.password};
            this.http
              .post<{message: number}>("http://localhost:3000/api/posts", lg)
              .subscribe(responseData => {
                if(responseData.message==1){
                  alert("succes");
                  this.router.navigate(['/MRlogin/home']);
                  localStorage['login_status'] = '1'
                  localStorage['username'] = responseData['data'][0].username
                  localStorage['id'] = responseData['data'][0].id
                  localStorage['flag'] = '0'
                }
                if(responseData.message!=1){
                  alert("invalid credentials");
                }

              });

    }
  }


}
