import { Injectable } from '@angular/core';

import { HttpClient } from '@angular/common/http';
import { Product } from './yoga.model';
import { Subject } from "rxjs";
import { map } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})
export class YogaService {

  private prods: Product[] = [];
  private postsUpdated = new Subject<Product[]>();

    constructor(private http: HttpClient) { }

    Data: any;
        getPosts() {
          this.http
            .get<{ message: string; prods: any }>(
              "http://localhost:3000/api/yogas"
            )
                 .subscribe(responseData => {

                  console.log(responseData.prods);
                  this.Data = responseData.prods;
                      });
          }


        getPostUpdateListener() {
          return this.postsUpdated.asObservable();
        }



}

