import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { map } from 'rxjs/operators';

import { Sched } from './Schedule.model';


@Injectable()
export class CartService {


    private posts: Sched[] = [];
    private postsUpdated = new Subject<Sched[]>();

    constructor(private http: HttpClient) { }

   /* getCart(mrid:number)
    {
        const body = {
            mrid:mrid
        }
        return this.httpClient.post(this.url,body)
    }*/
    getPostUpdateListener() {
      // this returns postsUpdated as Observable()
      return this.postsUpdated.asObservable();

    }
    addUsers(day:String, title:String,count:String
      ){


          const sched: Sched = {
            id: null,
            day:day,
            title:title,
            count:count,
             };
          this.http
            .post<{ message: string }>("http://localhost:3000/api/new-schedules", sched)
            .subscribe(responseData => {
              console.log(responseData.message);
              this.posts.push(sched);
              this.postsUpdated.next([...this.posts]);

            });

            

}
}
